import { useMemo, useCallback } from "react";
import { DeckGL } from "@deck.gl/react";
import { OrthographicView, PickingInfo } from "@deck.gl/core";
import { BitmapLayer } from "@deck.gl/layers";
import { useViewerStore, Panel as PanelType } from "../stores/viewerStore";
import { dataToRGBA } from "../utils/colormap";

// ISMIP6 Antarctic data grid parameters (EPSG:3031)
const GRID_WIDTH = 761;
const GRID_HEIGHT = 761;
const CELL_SIZE = 8000; // 8km in meters

// Grid extent in EPSG:3031 coordinates
const X_MIN = -3040000;
const Y_MIN = -3040000;
const X_MAX = X_MIN + GRID_WIDTH * CELL_SIZE;
const Y_MAX = Y_MIN + GRID_HEIGHT * CELL_SIZE;

// Center of the grid
const CENTER_X = (X_MIN + X_MAX) / 2;
const CENTER_Y = (Y_MIN + Y_MAX) / 2;

const INITIAL_VIEW_STATE = {
  target: [CENTER_X, CENTER_Y, 0] as [number, number, number],
  zoom: -13,
  minZoom: -16,
  maxZoom: 0,
};

interface PanelProps {
  panel: PanelType;
  isActive: boolean;
  canRemove: boolean;
}

export function Panel({ panel, isActive, canRemove }: PanelProps) {
  const {
    colormap,
    vmin,
    vmax,
    viewState,
    setViewState,
    setActivePanel,
    removePanel,
    setPanelModel,
    setPanelExperiment,
    loadPanelData,
    models,
    experiments,
    panels,
    hoverGridPosition,
    hoveredPanelId,
    setHoverGridPosition,
    getValueAtGridPosition,
  } = useViewerStore();

  const { currentData, dataShape, selectedModel, selectedExperiment, isLoading, error } = panel;

  const availableExperiments = selectedModel
    ? experiments.get(selectedModel) || []
    : [];

  const imageData = useMemo(() => {
    if (!currentData || !dataShape) return null;

    const [height, width] = dataShape;
    const rgba = dataToRGBA(currentData, width, height, vmin, vmax, colormap);

    // Create ImageData for BitmapLayer
    const canvas = document.createElement("canvas");
    canvas.width = width;
    canvas.height = height;
    const ctx = canvas.getContext("2d");
    if (!ctx) return null;

    const imgData = ctx.createImageData(width, height);
    imgData.data.set(rgba);
    ctx.putImageData(imgData, 0, 0);

    // Flip vertically for proper orientation
    const flippedCanvas = document.createElement("canvas");
    flippedCanvas.width = width;
    flippedCanvas.height = height;
    const flippedCtx = flippedCanvas.getContext("2d");
    if (!flippedCtx) return null;

    flippedCtx.translate(0, height);
    flippedCtx.scale(1, -1);
    flippedCtx.drawImage(canvas, 0, 0);

    // Return the canvas element directly instead of a data URL.
    // This avoids async image decode races that cause "attempt to
    // use a deleted object" WebGL errors when deck.gl replaces the
    // BitmapLayer texture on rapid updates.
    return flippedCanvas;
  }, [currentData, dataShape, colormap, vmin, vmax]);

  const onHover = useCallback(
    (info: PickingInfo) => {
      const { coordinate } = info;
      if (!coordinate) {
        setHoverGridPosition(null, null);
        return;
      }

      const [worldX, worldY] = coordinate;
      const gridX = Math.floor((worldX - X_MIN) / CELL_SIZE);
      const gridY = Math.floor((worldY - Y_MIN) / CELL_SIZE);

      if (gridX < 0 || gridX >= GRID_WIDTH || gridY < 0 || gridY >= GRID_HEIGHT) {
        setHoverGridPosition(null, null);
        return;
      }

      setHoverGridPosition({ gridX, gridY }, panel.id);
    },
    [setHoverGridPosition, panel.id]
  );

  // Get values from all panels at the current hover position
  const hoverValues = useMemo(() => {
    if (!hoverGridPosition) return null;
    const { gridX, gridY } = hoverGridPosition;

    return panels
      .filter((p) => p.currentData && p.selectedModel && p.selectedExperiment)
      .map((p) => ({
        panelId: p.id,
        model: p.selectedModel!,
        experiment: p.selectedExperiment!,
        value: getValueAtGridPosition(p.id, gridX, gridY),
      }));
  }, [hoverGridPosition, panels, getValueAtGridPosition]);

  const layers = useMemo(() => {
    if (!imageData) return [];

    return [
      new BitmapLayer({
        id: `data-layer-${panel.id}`,
        bounds: [X_MIN, Y_MIN, X_MAX, Y_MAX],
        image: imageData,
        pickable: false,
      }),
    ];
  }, [imageData, panel.id]);

  const views = new OrthographicView({
    id: "ortho",
    flipY: false,
  });

  // Use shared view state for linked zoom/pan
  const currentViewState = viewState || INITIAL_VIEW_STATE;

  const onViewStateChange = useCallback(
    ({ viewState: newViewState }: { viewState: typeof INITIAL_VIEW_STATE }) => {
      setViewState({
        target: newViewState.target,
        zoom: newViewState.zoom,
      });
    },
    [setViewState]
  );

  return (
    <div
      style={{
        width: "100%",
        height: "100%",
        position: "relative",
        border: isActive ? "2px solid #1976d2" : "1px solid #e0e0e0",
        borderRadius: "4px",
        overflow: "hidden",
      }}
      onClick={() => setActivePanel(panel.id)}
    >
      {/* Panel header with model/experiment selectors */}
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          right: 0,
          zIndex: 100,
          background: "rgba(255,255,255,0.95)",
          padding: "8px 12px",
          borderBottom: "1px solid #e0e0e0",
          display: "flex",
          alignItems: "center",
          gap: "8px",
        }}
      >
        <select
          value={selectedModel || ""}
          onChange={(e) => setPanelModel(panel.id, e.target.value)}
          style={{
            padding: "4px 8px",
            borderRadius: "4px",
            border: "1px solid #ccc",
            fontSize: "12px",
            flex: 1,
            minWidth: 0,
          }}
        >
          <option value="">Model...</option>
          {models.map((model) => (
            <option key={model} value={model}>
              {model}
            </option>
          ))}
        </select>

        <select
          value={selectedExperiment || ""}
          onChange={(e) => setPanelExperiment(panel.id, e.target.value)}
          disabled={!selectedModel}
          style={{
            padding: "4px 8px",
            borderRadius: "4px",
            border: "1px solid #ccc",
            fontSize: "12px",
            flex: 1,
            minWidth: 0,
          }}
        >
          <option value="">Experiment...</option>
          {availableExperiments.map((exp) => (
            <option key={exp} value={exp}>
              {exp}
            </option>
          ))}
        </select>

        <button
          onClick={(e) => {
            e.stopPropagation();
            loadPanelData(panel.id);
          }}
          disabled={isLoading || !selectedModel || !selectedExperiment}
          style={{
            padding: "4px 12px",
            backgroundColor:
              isLoading || !selectedModel || !selectedExperiment
                ? "#ccc"
                : "#1976d2",
            color: "white",
            border: "none",
            borderRadius: "4px",
            fontSize: "12px",
            cursor:
              isLoading || !selectedModel || !selectedExperiment
                ? "not-allowed"
                : "pointer",
          }}
        >
          {isLoading ? "..." : "Load"}
        </button>

        {canRemove && (
          <button
            onClick={(e) => {
              e.stopPropagation();
              removePanel(panel.id);
            }}
            style={{
              padding: "4px 8px",
              backgroundColor: "#f44336",
              color: "white",
              border: "none",
              borderRadius: "4px",
              fontSize: "12px",
              cursor: "pointer",
            }}
          >
            X
          </button>
        )}
      </div>

      {/* Map view */}
      <div
        style={{ position: "absolute", top: "48px", left: 0, right: 0, bottom: 0 }}
        onMouseLeave={() => setHoverGridPosition(null, null)}
      >
        <DeckGL
          views={views}
          viewState={currentViewState}
          onViewStateChange={onViewStateChange}
          controller={true}
          layers={layers}
          style={{ background: "#1a1a2e" }}
          onHover={onHover}
        />

        {/* Loading overlay */}
        {isLoading && (
          <div
            style={{
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              background: "rgba(255,255,255,0.9)",
              padding: "12px 20px",
              borderRadius: "4px",
              fontSize: "14px",
            }}
          >
            Loading...
          </div>
        )}

        {/* Error overlay */}
        {error && (
          <div
            style={{
              position: "absolute",
              bottom: "50px",
              left: "10px",
              right: "10px",
              background: "#ffebee",
              color: "#c62828",
              padding: "8px",
              borderRadius: "4px",
              fontSize: "12px",
            }}
          >
            {error}
          </div>
        )}

        {/* No data message */}
        {!currentData && !isLoading && (
          <div
            style={{
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              textAlign: "center",
              color: "white",
            }}
          >
            <div style={{ fontSize: "14px", opacity: 0.7 }}>
              Select model/experiment and click Load
            </div>
          </div>
        )}

        {/* Hover tooltip - show values from all panels (only on hovered panel) */}
        {hoveredPanelId === panel.id && hoverGridPosition && hoverValues && hoverValues.length > 0 && (
          <div
            style={{
              position: "absolute",
              left: "50%",
              bottom: "50px",
              transform: "translateX(-50%)",
              background: "rgba(0, 0, 0, 0.9)",
              color: "white",
              padding: "8px 12px",
              borderRadius: "4px",
              fontSize: "11px",
              pointerEvents: "none",
              whiteSpace: "nowrap",
              zIndex: 200,
            }}
          >
            <div style={{ marginBottom: "4px", opacity: 0.7, fontSize: "10px" }}>
              Grid: ({hoverGridPosition.gridX}, {hoverGridPosition.gridY})
            </div>
            {hoverValues.map((hv) => (
              <div
                key={hv.panelId}
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  gap: "12px",
                  padding: "2px 0",
                  borderBottom: hv.panelId === panel.id ? "1px solid #666" : "none",
                  fontWeight: hv.panelId === panel.id ? "bold" : "normal",
                }}
              >
                <span style={{ opacity: 0.8 }}>
                  {hv.model}/{hv.experiment}:
                </span>
                <span>
                  {hv.value !== null ? hv.value.toFixed(2) : "N/A"}
                </span>
              </div>
            ))}
          </div>
        )}

        {/* Panel label */}
        {currentData && (
          <div
            style={{
              position: "absolute",
              bottom: "8px",
              left: "8px",
              background: "rgba(255,255,255,0.9)",
              padding: "4px 8px",
              borderRadius: "4px",
              fontSize: "11px",
            }}
          >
            {selectedModel} / {selectedExperiment}
          </div>
        )}
      </div>
    </div>
  );
}
